import axios from 'axios'
import { apiDomain,tagUrl, akiraUrl,starUrl, directorUrl,topicUrl} from "./config"




export function getHotTypeList(type) {

  return axios.get(apiDomain +'api/'+ type + '/hot').then((res) => {
    return res.data
  })

}

export function getTopicList() {
  return axios.get(topicUrl).then((res) => {
    return res.data;
  })

}



export function getShowTypeList(id,type) {

  return axios.get(apiDomain +'api/admin/'+ type  + '/' +id).then((res) => {
    return res.data
  })

}

export function getTypeList(type,skip) {

  return axios({
    method: 'get',
    url:  apiDomain + 'api/admin/'+ type,
    params:{
      skip: skip,
    },
  }).then((res) => {
    return res.data
  })

}


export function getAkiraList() {
  return axios.get(akiraUrl).then((res) => {
    return res.data;
  })

}

export function getStarList() {
  return axios.get(starUrl).then((res) => {
    return res.data;
  })

}

export function getDirectorList() {
  return axios.get(directorUrl).then((res) => {
    return res.data;
  })

}


export function getTagList() {
  return axios.get(tagUrl).then((res) => {
    return res.data;
  })

}


export function postTypeList(formData,type) {

  return axios({
    method: 'post',
    url: apiDomain + 'api/admin/'+ type,
    data: formData
  }).then((res) => {

    return res.data;
  })
}

export function postUpdateTypeList(formData,type) {

  return axios({
    method: 'patch',
    url: apiDomain + 'api/admin/'+ type + '/' + formData.id,
    data: formData
  }).then((res) => {
    return res.data;
  })
}

export function deleteType(id,type) {
  return axios({
    method: 'delete',
    url: apiDomain + 'api/admin/'+ type + '/' + id,
  }).then((res) => {
    return res.data;
  })
}




